import React, {useEffect, useState} from 'react';
import {Button, Input, message} from "antd";

const Test = () => {

    const [ping,setPing] = useState({});
    const [value,setValue] = useState("")

    var fifty = [
        {text:'あ',read:'a'},{text:'い',read: 'i'},{text: 'う',read: 'u'},{text: 'え',read: 'e'},{text: 'お',read: 'o'},
        {text:'か',read:'ka'},{text:'き',read: 'ki'},{text: 'く',read: 'ku'},{text: 'け',read: 'ke'},{text: 'こ',read: 'ko'},
        {text:'さ',read:'sa'},{text:'し',read: 'shi'},{text: 'す',read: 'su'},{text: 'せ',read: 'se'},{text: 'そ',read: 'so'},
        {text:'た',read:'ta'},{text:'ち',read: 'chi'},{text: 'つ',read: 'tsu'},{text: 'て',read: 'te'},{text: 'と',read: 'to'},
        {text:'な',read:'na'},{text:'に',read: 'ni'},{text: 'ぬ',read: 'nu'},{text: 'ね',read: 'ne'},{text: 'の',read: 'no'},
        {text:'は',read:'ha'},{text:'ひ',read: 'hi'},{text: 'ふ',read: 'hu'},{text: 'へ',read: 'he'},{text: 'ほ',read: 'ho'},
        {text:'ま',read:'ma'},{text:'み',read: 'mi'},{text: 'む',read: 'mu'},{text: 'め',read: 'me'},{text: 'も',read: 'mo'},
        {text:'や',read:'ya'},{text:'い',read: 'i'},{text: 'ゆ',read: 'yu'},{text: 'え',read: 'e'},{text: 'よ',read: 'yo'},
        {text:'ら',read:'ra'},{text:'り',read: 'ri'},{text: 'る',read: 'ru'},{text: 'れ',read: 're'},{text: 'ろ',read: 'ro'},
        {text:'わ',read:'wa'},{text:'い',read: 'i'},{text: 'う',read: 'u'},{text: 'え',read: 'e'},{text: 'を',read: 'wo'},
    ]

    const random = () => {
        let index = Math.floor((Math.random()*fifty.length));
        console.log(fifty[index])
        setPing(fifty[index])
    }

    useEffect(()=>{
        random();
    },[])

    const match = () => {
        if (value === ping.read) {
           message.success('right');
        }else{
            message.error('error')
        }
    }

    return (
        <div>
            <div style={{border:"5px solid",marginLeft:"40vw",marginRight:"40vw",paddingBottom:"5vw",borderRadius:"20px",marginTop:"7vw"}}>
                <div style={{fontSize:"150px"}}>
                    {ping.text}
                </div>
                <div style={{display:"inline-flex",marginRight:"auto",marginLeft:"auto"}}>
                    <div style={{width:"10vw"}}>
                        <Input onChange={(event)=>{setValue(event.target.value)}}/>
                    </div>
                    <Button onClick={()=>{random();match()}}>next</Button>
                </div>
            </div>
        </div>
    );
};

export default Test;
